
import React from 'react';
import utilityServices from '../../../services/utils/utilityServices';
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';

// react-icons
import { FaCheck } from 'react-icons/fa';

export default function AttachmentTypeForm(props) {

    const attachmentTypeRedux = useSelector((state) => state.attachmentTypes.value);
    const [attachmentType, setAttachmentType] = React.useState(attachmentTypeRedux.selectedAttachmentType?.type?.toUpperCase() ?? "");
    const [attachmentTypeDescription, setAttachmentTypeDescription] = React.useState(attachmentTypeRedux.selectedAttachmentType?.description ?? "");

    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            "data": {
                "type": attachmentType,
                "description": attachmentTypeDescription
            },

            "params": attachmentTypeRedux.selectedAttachmentType
        }

        props.handleSubmit(data);
    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setAttachmentType(e.target.value)}
                                value={attachmentType}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="attachmentType"
                                label="Attachment Type"
                                name="attachmentType"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setAttachmentTypeDescription(e.target.value)}
                                value={attachmentTypeDescription}
                                variant="outlined"
                                size="small"
                                multiline
                                maxRows={5}
                                minRows={5}
                                required
                                fullWidth
                                id="description"
                                label="Description"
                                name="description"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={attachmentTypeRedux.loading}
                            >
                                {utilityServices.capitalizeFirstLetter(props.action)}
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
