
import React from 'react';
import utilityServices from '../../../services/utils/utilityServices';
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';

// react-icons
import { FaCheck } from 'react-icons/fa';
import Period from './fields/Period';

export default function LoanTypeForm(props) {

    const loanTypeRedux = useSelector((state) => state.loanTypes.value);
    const [loanType, setLoanType] = React.useState(loanTypeRedux.selectedLoanType?.type?.toUpperCase() ?? "");
    const [loanTypeRate, setLoanTypeRate] = React.useState(loanTypeRedux.selectedLoanType?.rates ?? "");
    const [loanTypePeriod, setLoanTypePeriod] = React.useState(loanTypeRedux.selectedLoanType?.period ?? "");
    const [loanTypeDescription, setLoanTypeDescription] = React.useState(loanTypeRedux.selectedLoanType?.description ?? "");

    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            "data": {
                "type": loanType,
                "rates": loanTypeRate,
                "period": loanTypePeriod,
                "description": loanTypeDescription
            },

            "params": loanTypeRedux.selectedLoanType
        }

        props.handleSubmit(data);
    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setLoanType(e.target.value)}
                                value={loanType}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="loanType"
                                label="Loan Type"
                                name="loanType"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setLoanTypeRate(e.target.value)}
                                value={parseInt(loanTypeRate)}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="loanTypeRate"
                                label="Rate"
                                name="loanTypeRate"
                                type="number"
                                InputProps={{
                                    inputProps: {
                                        max: 100, min: 0
                                    }
                                }}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <Period period={loanTypePeriod} setPeriod={setLoanTypePeriod} />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setLoanTypeDescription(e.target.value)}
                                value={loanTypeDescription}
                                variant="outlined"
                                size="small"
                                multiline
                                maxRows={5}
                                minRows={5}
                                required
                                fullWidth
                                id="description"
                                label="Description"
                                name="description"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={loanTypeRedux.loading}
                            >
                                {utilityServices.capitalizeFirstLetter(props.action)}
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
