
import React from 'react';
import utilityServices from '../../../services/utils/utilityServices';
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';

// react-icons
import { FaCheck } from 'react-icons/fa';

export default function EmploymentTypeForm(props) {

    const employmentTypeRedux = useSelector((state) => state.employmentTypes.value);
    const [employmentType, setEmploymentType] = React.useState(employmentTypeRedux.selectedEmploymentType?.type?.toUpperCase() ?? "");
    const [employmentTypeDescription, setEmploymentTypeDescription] = React.useState(employmentTypeRedux.selectedEmploymentType?.description ?? "");

    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            "data": {
                "type": employmentType,
                "description": employmentTypeDescription
            },

            "params": employmentTypeRedux.selectedEmploymentType
        }

        props.handleSubmit(data);
    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setEmploymentType(e.target.value)}
                                value={employmentType}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="employmentType"
                                label="Employment Type"
                                name="employmentType"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setEmploymentTypeDescription(e.target.value)}
                                value={employmentTypeDescription}
                                variant="outlined"
                                size="small"
                                multiline
                                maxRows={5}
                                minRows={5}
                                required
                                fullWidth
                                id="description"
                                label="Description"
                                name="description"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={employmentTypeRedux.loading}
                            >
                                {utilityServices.capitalizeFirstLetter(props.action)}
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
