import React from 'react';

// Utilities 
import utilityService from '../../../services/utils/utilityServices';
import alertService from '../../../services/alert/alertService';

// Redux
import { useSelector, useDispatch } from 'react-redux';
import { destroy, get } from '../../../redux/async/accountTypeAsync';
import { selectedAccountType } from '../../../redux/slices/accountTypeSlice';

// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';

// Page Components
import GridLoadingOverlay from '../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../grid/GridNoRowOverlay';
import Toolbar from '../grid/Toolbar';
import AccountTypeModal from '../modals/AccountTypeModal';

// react-icons
import { FaEdit, FaTrash } from 'react-icons/fa';


const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar
            onChange={props.onChange}
            isMultipleDelete={props.isMultipleDelete}
            create={props.create}
            delete={props.delete}
        />
    );
}

export default function AccountTypeDataTable(props) {

    const dispatch = useDispatch();
    const accountTypeRedux = useSelector((state) => state.accountTypes.value);
    const loading = accountTypeRedux.loading;
    const [rowData, setRowData] = React.useState([]);

    const [selected, setSelected] = React.useState([]);
    const [isMultipleDelete, setIsMultipleDelete] = React.useState(true);

    const [modalAction, setModalAction] = React.useState("");
    const [showAccountTypeModal, setShowAccountTypeModal] = React.useState(false);
    const toggleAccountTypeModal = () => setShowAccountTypeModal(!showAccountTypeModal);

    const columns = [
        {
            field: 'type', headerName: 'Type', width: 250, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.type.toUpperCase()}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'description', headerName: 'Description', flex: 1, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.description === null ? "..." : params.row.description}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'actions', headerName: 'Options', type: 'actions', getActions: (params) => [
                <GridActionsCellItem
                    icon={<FaEdit size={18} />}
                    onClick={() => handleEdit(params)}
                    label="Edit"
                    sx={{
                        '&:hover': {
                            color: "#00BFA5"
                        }
                    }}
                    showInMenu
                />,
                <GridActionsCellItem
                    icon={<FaTrash size={15} />}
                    onClick={() => handleDelete(1, params.row.id)}
                    label="Delete"
                    sx={{
                        '&:hover': {
                            color: "#E45570"
                        }
                    }}
                    showInMenu
                />
            ]
        },
    ];

    const handleSelection = (value) => {

        setSelected(value);

        if (value.length > 0) {
            setIsMultipleDelete(false);
        } else {
            setIsMultipleDelete(true);
        }
    }

    const handleEdit = async (params) => {
        dispatch(selectedAccountType(params.row));
        setModalAction("edit");
        setShowAccountTypeModal(true);
    }

    const handleDelete = async (count = 0, row = null) => {

        let rows = selected

        if (row !== null) {
            rows = [row];
        }

        let recordCount = count > 0 ? 1 : rows.length;
        let response = await alertService.delete(recordCount);
        if (response.isConfirmed) {
            dispatch(destroy({ 'ids': rows }))
        }
    }

    const search = (text) => {
        let filtered = utilityService.filterArrayOfObjects(text, accountTypeRedux.accountTypes);
        setRowData(filtered);
    };

    const loadAccountType = React.useCallback(() => {
        dispatch(get());
    }, [dispatch])

    React.useEffect(() => {
        let mounted = false;
        
        if(!mounted){
            loadAccountType();
        }

        return () => {
            mounted = true;
        }
      
    }, [loadAccountType]);

    return (
        <div  style={{ height: '100%', width: '100%' }}>
            {showAccountTypeModal && <AccountTypeModal open={showAccountTypeModal} toggle={toggleAccountTypeModal} action={modalAction} />}
            <DataGrid
                rows={rowData.length <= 0 ? accountTypeRedux.accountTypes : rowData}
                columns={columns}
                selectionModel={selected}
                onSelectionModelChange={handleSelection}
                checkboxSelection={true}
                loading={loading}
                density="standard"
                disableSelectionOnClick
                disableColumnMenu
                components={{
                    LoadingOverlay: LoadingOverlayOverride,
                    NoRowsOverlay: NoRowOverlayOverride,
                    Toolbar: ToolbarOverride
                }}
                componentsProps={{
                    toolbar: {
                        create: () => { setModalAction("create"); setShowAccountTypeModal(true); dispatch(selectedAccountType({})); },
                        delete: () => { handleDelete() },
                        isMultipleDelete: isMultipleDelete,
                        onChange: (event) => search(event.target.value),
                    },
                }}
            />
        </div>
    );
}