
import React from 'react';
import utilityServices from '../../../services/utils/utilityServices';
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';

// react-icons
import { FaCheck } from 'react-icons/fa';

export default function PaymentTypeForm(props) {

    const paymentTypeRedux = useSelector((state) => state.paymentTypes.value);
    const [paymentType, setPaymentType] = React.useState(paymentTypeRedux.selectedPaymentType?.type?.toUpperCase() ?? "");
    const [paymentTypeDescription, setPaymentTypeDescription] = React.useState(paymentTypeRedux.selectedPaymentType?.description ?? "");

    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            "data": {
                "type": paymentType,
                "description": paymentTypeDescription
            },

            "params": paymentTypeRedux.selectedPaymentType
        }

        props.handleSubmit(data);
    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setPaymentType(e.target.value)}
                                value={paymentType}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="paymentType"
                                label="Payment Type"
                                name="paymentType"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setPaymentTypeDescription(e.target.value)}
                                value={paymentTypeDescription}
                                variant="outlined"
                                size="small"
                                multiline
                                maxRows={5}
                                minRows={5}
                                required
                                fullWidth
                                id="description"
                                label="Description"
                                name="description"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={paymentTypeRedux.loading}
                            >
                                {utilityServices.capitalizeFirstLetter(props.action)}
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
