import React from 'react';

// Utilities 
import utilityService from '../../../services/utils/utilityServices';
import alertService from '../../../services/alert/alertService';

// Redux
import { useSelector, useDispatch } from 'react-redux';
import { destroy, get } from '../../../redux/async/organizationTypesAsync';
import { selectedOrganizationType } from '../../../redux/slices/organizationTypesSlice';

// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';

// Page Components
import GridLoadingOverlay from '../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../grid/GridNoRowOverlay';
import Toolbar from '../grid/Toolbar';
import OrganizationTypesModal from '../modals/OrganizationTypesModal';

// react-icons
import { FaEdit, FaTrash } from 'react-icons/fa';


const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar
            onChange={props.onChange}
            isMultipleDelete={props.isMultipleDelete}
            create={props.create}
            delete={props.delete}
        />
    );
}

export default function OrganizationTypesDataTable(props) {

    const dispatch = useDispatch();
    const organizationTypesRedux = useSelector((state) => state.organizationTypes.value);
    const loading = organizationTypesRedux.loading;
    const [rowData, setRowData] = React.useState([]);

    const [selected, setSelected] = React.useState([]);
    const [isMultipleDelete, setIsMultipleDelete] = React.useState(true);

    const [modalAction, setModalAction] = React.useState("");
    const [showOrganizationTypeModal, setShowOrganizationTypeModal] = React.useState(false);
    const toggleOrganizationTypeModal = () => setShowOrganizationTypeModal(!showOrganizationTypeModal);

    const columns = [
        {
            field: 'type', headerName: 'Type', width: 250, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.type.toUpperCase()}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'description', headerName: 'Description', flex: 1, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.description === null ? "..." : params.row.description}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'actions', headerName: 'Options', type: 'actions', getActions: (params) => [
                <GridActionsCellItem
                    icon={<FaEdit size={18} />}
                    onClick={() => handleEdit(params)}
                    label="Edit"
                    sx={{
                        '&:hover': {
                            color: "#00BFA5"
                        }
                    }}
                    showInMenu
                />,
                <GridActionsCellItem
                    icon={<FaTrash size={15} />}
                    onClick={() => handleDelete(1, params.row.id)}
                    label="Delete"
                    sx={{
                        '&:hover': {
                            color: "#E45570"
                        }
                    }}
                    showInMenu
                />
            ]
        },
    ];

    const handleSelection = (value) => {

        setSelected(value);

        if (value.length > 0) {
            setIsMultipleDelete(false);
        } else {
            setIsMultipleDelete(true);
        }
    }

    const handleEdit = async (params) => {
        dispatch(selectedOrganizationType(params.row));
        setModalAction("edit");
        setShowOrganizationTypeModal(true);
    }

    const handleDelete = async (count = 0, row = null) => {

        let rows = selected

        if (row !== null) {
            rows = [row];
        }

        let recordCount = count > 0 ? 1 : rows.length;
        let response = await alertService.delete(recordCount);
        if (response.isConfirmed) {
            dispatch(destroy({ 'ids': rows }))
        }
    }

    const search = (text) => {
        let filtered = utilityService.filterArrayOfObjects(text, organizationTypesRedux.organizationTypes);
        setRowData(filtered);
    };

    const loadOrganizationType = React.useCallback(() => {
        dispatch(get());
    }, [dispatch])

    React.useEffect(() => {
        
        let mounted = false;

        if (!mounted) {
            loadOrganizationType();
        }

        return () => mounted = true;
     
    }, [loadOrganizationType]);

    return (
        <div style={{ height: '100%', width: '100%' }}>
            {showOrganizationTypeModal && <OrganizationTypesModal open={showOrganizationTypeModal} toggle={toggleOrganizationTypeModal} action={modalAction} />}
            <DataGrid
                rows={rowData.length <= 0 ? organizationTypesRedux.organizationTypes : rowData}
                columns={columns}
                selectionModel={selected}
                onSelectionModelChange={handleSelection}
                checkboxSelection={true}
                loading={loading}
                density="standard"
                disableSelectionOnClick
                disableColumnMenu
                components={{
                    LoadingOverlay: LoadingOverlayOverride,
                    NoRowsOverlay: NoRowOverlayOverride,
                    Toolbar: ToolbarOverride
                }}
                componentsProps={{
                    toolbar: {
                        create: () => { setModalAction("create"); setShowOrganizationTypeModal(true); dispatch(selectedOrganizationType({})); },
                        delete: () => { handleDelete() },
                        isMultipleDelete: isMultipleDelete,
                        onChange: (event) => search(event.target.value),
                    },
                }}
            />
        </div>
    );
}