import React from 'react'
import utilityService from '../../../services/utils/utilityServices';

// Redux
import { useDispatch } from 'react-redux';
import { post, put } from '../../../redux/async/positionAsync';

// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog'; 
import PositionForm from '../forms/PositionForm';

export default function PositionModal(props) {

    const title = utilityService.capitalizeFirstLetter(props.action);
    const dispatch = useDispatch();

    const handleSubmit = (params) => {
        if (props.action === "create") {
            dispatch(post(params));
        } else {            
            dispatch(put(params));
        }
    }

    return (
        <div>
            <InnerModalDialog open={props.open} title={`${title} Position`} toggle={props.toggle}>
                <PositionForm action={props.action} handleSubmit={handleSubmit} />
            </InnerModalDialog>
        </div>
    )
}
