
import React from 'react';
import utilityServices from '../../../services/utils/utilityServices';
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';

// react-icons
import { FaCheck } from 'react-icons/fa';

export default function TransactionTypeForm(props) {

    const transactionTypeRedux = useSelector((state) => state.transactionTypes.value);
    const [transactionType, setTransactionType] = React.useState(transactionTypeRedux.selectedTransactionType?.type?.toUpperCase() ?? "");
    const [transactionTypeDescription, setTransactionTypeDescription] = React.useState(transactionTypeRedux.selectedTransactionType?.description ?? "");

    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            "data": {
                "type": transactionType,
                "description": transactionTypeDescription
            },

            "params": transactionTypeRedux.selectedTransactionType
        }

        props.handleSubmit(data);
    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setTransactionType(e.target.value)}
                                value={transactionType}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="transactionType"
                                label="Transaction Type"
                                name="transactionType"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setTransactionTypeDescription(e.target.value)}
                                value={transactionTypeDescription}
                                variant="outlined"
                                size="small"
                                multiline
                                maxRows={5}
                                minRows={5}
                                required
                                fullWidth
                                id="description"
                                label="Description"
                                name="description"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={transactionTypeRedux.loading}
                            >
                                {utilityServices.capitalizeFirstLetter(props.action)}
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
