
import React from 'react';
import utilityServices from '../../../services/utils/utilityServices';
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';

// react-icons
import { FaCheck } from 'react-icons/fa';

export default function AccountTypeForm(props) {

    const accountTypeRedux = useSelector((state) => state.accountTypes.value);
    const [accountType, setAccountType] = React.useState(accountTypeRedux.selectedAccountType?.type?.toUpperCase() ?? "");
    const [accountTypeDescription, setAccountTypeDescription] = React.useState(accountTypeRedux.selectedAccountType?.description ?? "");

    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            "data": {
                "type": accountType,
                "description": accountTypeDescription
            },

            "params": accountTypeRedux.selectedAccountType
        }

        props.handleSubmit(data);
    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setAccountType(e.target.value)}
                                value={accountType}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="accountType"
                                label="Account Type"
                                name="accountType"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setAccountTypeDescription(e.target.value)}
                                value={accountTypeDescription}
                                variant="outlined"
                                size="small"
                                multiline
                                maxRows={5}
                                minRows={5}
                                required
                                fullWidth
                                id="description"
                                label="Description"
                                name="description"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={accountTypeRedux.loading}
                            >
                                {utilityServices.capitalizeFirstLetter(props.action)}
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
